.ProjectsContainer{
    margin-left: auto;
    margin-right: auto;
    justify-content: space-around;
    max-width: 1110px;
    display: flex;
    margin-bottom: 100px;
}

.Projects p:nth-child(1){
    text-align: center;
    margin-top: 50px;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 48px;
}

.ProjectsContainer{
    display: flex;
}

.Project{
    width: 40%;
}

.Project img{
    border-radius: 10px;
    width: 100%;
}