.Image img{
    border-radius: 10px;
    width: 100%;
}

.Image{
    display: flex;
    margin-left: 50px;
    width: 30%;
}

.AboutMe{
    align-items: center;
    justify-content: center;
    display: flex;
    margin-right: auto;
    margin-left: auto;
    max-width: 1110px;
    margin-top: 150px;
}

.Text p:nth-child(1){
    text-align: center;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-size: 20px;
    color: black;
}

.Text p{
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    color: #727272;
    text-align: left;
}

.Text{
    width: 50%;
}

@media screen and (max-width: 890px){
    .Image{
        width: 40%;
    }
    .Text{
        width: 50%;
    }
}

@media screen and (max-width: 815px) {
    .AboutMe{
        flex-direction: column-reverse;
        align-items: center;
    }
    .Text p{
        text-align: justify;
    }
    .Image{
        margin-left: 0;
    }
}

@media screen and (max-width: 650px) {
    .Text p{
        font-size: 14px;
    }
    .Text p:nth-child(1){
        font-size: 16px;
    }
}