.Footer{
    border-top: #444444 solid 1px;
    background: #34547A;
}

.Info{

}

.Info p{
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 21px;
    color: white;
}

.Contacts img{
    width: 100px;
}

.Contacts{
    width: 200px;
}

.Container{
    justify-content: space-around;
    display: flex;
    align-items: center;
    max-width: 1110px;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (max-width: 720px){
    .Info p{
        font-size: 18px;
    }
    .Contacts img{
        width: 80px;
    }
    .Contacts{
        width: 160px;
    }
}

@media screen and (max-width: 500px){
    .Info p{
        font-size: 14px;
    }
    .Contacts img{
        width: 60px;
    }
    .Contacts{
        width: 120px;
    }
}

