.MainPageInfo{
    max-width: 1110px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    margin-top: 200px;
    justify-content: center;
    align-items: center;
    margin-bottom: 200px;
}

.Image img{
    width: 100%;
}

.Image{
    width: 30%;
}

.Text{
    margin-left: 10%;
    width: 40%;
}

.Text h3{
    margin-bottom: 30px;
    margin-top: 0;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 48px;
}

.Text p{
    margin-top: 0;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #727272;
}

.Text button{
    margin-top: 10px;
    width: 195px;
    height: 45px;
    background: #34547A;
    border: none;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    text-align: center;
    letter-spacing: 0.1em;
    color: #FFFFFF;
}

@media screen and (max-width: 980px){
    .Text{
        margin-left: 0;
        margin-top: 50px;
    }
    .MainPageInfo{
        flex-direction: column;
    }
    .Text{
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .Text h3{
        text-align: center;
    }
    .Text p{
        text-align: center;
    }
    .Image{
        min-width: 196px;
        width: 40%;
    }
}
