.Skills{
    flex-direction: row;
    display: flex;
    max-width: 1110px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 50px;
    align-items: center;
    padding-bottom: 100px;
}

.Adaptive{
    margin-left: auto;
    margin-right: auto;
    width: 80%;
}

.Adaptive img{
    width: 100%;
    border-radius: 10px;
}

.Skills p{
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    text-align: center;

}

@media screen and (max-width: 680px) {
    .Skills p{
        font-size: 20px;
    }
}