.Nav{
    max-width: 1110px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center
}

.Nav a{
    margin-top: 40px;
    text-decoration: none;
    color: #34547A;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.1em;
}

.Nav a{
    margin-right: 2%;
}

.Nav a:nth-child(4){
    margin-right: 0;
}

.Nav a:hover{
    color: cornflowerblue;
}

@media screen and (max-width: 600px){
    .Nav a{
        font-size: 14px;
    }
}